#page--weather {
    background-color: rgba(238, 255, 254, 0.918);
    border: 2px solid rgb(3, 4, 56);
    border-radius: 200px;
    font-family: "Century Gothic", CenturyGothic, AppleGothic, sans-serif;
    font-size: 35px;
    margin: auto;
    margin-top: 15vh;
    padding: 40px 0px 40px 0px;
    text-align: center;
    width: 750px;
}

#page--weather:hover {
    border-radius: 5px;
    transition: border-radius 1s;
}

.slot {
    background-color: rgba(255, 238, 254, 0.918);
    border: 2px solid rgb(56, 3, 28);
    margin: auto;
    margin-bottom: 15px;
    margin-top: 15px;
    width: 375px;
}

.slot:hover {
    border-radius: 50px;
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    transition: border-radius 1s, -webkit-transform .5s;
    transition: border-radius 1s, transform .5s;
    transition: border-radius 1s, transform .5s, -webkit-transform .5s;
}
#page--intro {
    margin: auto;
    padding-top: 35vh;
    width: 450px;
}

#button {
    background-color: rgba(228, 185, 236, 0.918);
    border: 1px groove rgb(32, 2, 32);
    border-radius: 15px;
    font-size: 30px;
    margin-left: 100px;
}

#button:hover {
    background-color: rgba(6, 1, 7, 0.801);
    color: rgb(240, 214, 245);
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}

#alert {
    font-size: 20px;
    margin-top: 30px;
    text-align: center;
}
